#section-community {
  text-align: center;

  #community-outer {
      padding: 158px 0 0; }

  .imagine-link {
    margin-left: 24px; }


  .community {
    position:  relative;
    margin: 20px 0px; }

  .community-img {
    position: relative;
    max-width: 100%;
    width: 100%;
    z-index: 10; }

  .dot {
    position: absolute;
    left: 8.1%;
    top: 7.8%;
    width: 9.5%;
    height: 42.3%; }

  .dot-1 {
    width: 9.3%; }

  .dot-2 {
    left: 22.2%;
    top: 28.5%;
    width: 9.2%; }

  .dot-3 {
    left: 36.2%;
    top: 49.5%;
    width: 9.2%; }

  .dot-4 {
    left: 45.5%;
    top: 49.4%;
    width: 9.2%; }

  .dot-5 {
    left: 45.5%;
