  .main-section-bottom {
    margin: 20px auto 0;
    max-width: 920px;
    font-family: $font-family-heading;

    font-style: normal;
    font-weight: 700;
    line-height: 25px;
    letter-spacing: 0.4000000059604645px;
    text-align: center;
    color: #fff;

    .main-section-bottom-bg {
      border-radius: 15px;
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0;

      padding: 36px 60px;
      background: #F47F57;
      font-size: 22px;
      line-height: 30px;
      // background: linear-gradient(45deg, #000000 14.64%, #515151 85.36%)
      // background: linear-gradient(45deg, #FF8B71 0%, #A81E41 100%)
      background: linear-gradient(128.31deg, #FF8B71 5.12%, #C33F50 51.15%, #A81E41 83.33%);

      @media (max-width: 400px) {
        font-size: 14px;
        padding: 15px; } } }

  .main-heading {
