
.product-section {
  background: linear-gradient(180deg, #212121 0%, rgba(33, 33, 33, 0) 60.94%);
  border-radius: 55px;
  margin: 0 0 60px 0;
  padding-top: 60px;
  text-align: center;

  .product_card_text {
    min-height: 104px; }

  @media (max-width: 740px) {
    padding: 30px 0px; }

  .card-title {
    margin: 48px 0px 8px 0px;
    font-size: 24px;
    line-height: 28px;
    min-height: 56px; }


  .product_title {
    text-align: center;

    @media (max-width: 400px) {
      font-size: 30px;
      padding: 15px; }

    &_text {
      // width: 753px
      font-family: $font-family-body;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      letter-spacing: 0px;
      text-align: center;
      color: rgba(161, 161, 161, 1);
      margin: 15px auto;

      @media (max-width: 400px) {
        font-size: 14px;
        padding: 15px; } } }

  .card-deck {
    margin-top: 60px;
    margin-bottom: 60px; }
  .card-bg {
    background: linear-gradient(180deg, #2D2D2D 0%, #171717 100%); }


  .product_card {
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    padding: 64px 40px 32px 40px;

    &_img {
      width: 96px;
      height: 96px;
 }      // margin: 71px 142px 0px 142px

    &_title {
      // width: 300px
      font-family: $font-family-heading;
      font-size: 24px;
      font-style: normal;
      font-weight: 700;
      line-height: 29px;
      letter-spacing: 0px;
      text-align: center;
      color: #ffffff;
      margin: 40px 0 20px; }

    &_text {
      font-size: 16px;
      font-style: normal;
      flex: 1;
      font-weight: 500;
      line-height: 25px;
      letter-spacing: 0em;
      text-align: center;
      color: rgba(150, 150, 150, 1); } }
  .theme-btn-outline {
    padding: 11px 15px; }

  .product_section_learnmore_link {
    // width: 130px
    font-family: $font-family-heading;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
    letter-spacing: 0px;
    text-align: center;
    color: #F47F57;
    text-decoration: none;
    margin-top: 24px;

    &.soon {
      color: rgb(66, 63, 63)!important;
      border: none;
      background: none; } }

  a {
    text-decoration: none; } }
