.fixed_header #header {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100; }

#header {
  background: rgba(0, 0, 0, 0.08);
  width: 100%;
  height: 80px;
  padding: 18px 0px;
  font-size: 14px;
  font-weight: 600;
  z-index: 100;
  backdrop-filter: blur(70px);

  a {
    color: #fff; }
  nav {
    padding: 0; }

  .nav-link {
    padding: 10px 16px;
    margin: 0px 4px; }

  .nav-link.btn-orange {
    margin-left: 8px;
    padding-left: 0px;
    padding-right: 0px;
    min-width: 110px; }

  .navbar-nav li:last-child a {
    margin-right: 0; }


  .nav-item.dropdown:hover #navbardrop {
    color: #fff;
    opacity: 0.88; }

  .nav-item.dropdown:hover .dropdown-menu {
    top: 53px;
    //transform: rotateX(15deg)//
    visibility: visible;
    opacity: 1;
    visibility: visible; }


  .dropdown-menu {
    font-size: 14px;
    background: linear-gradient(180deg, #48423E 0%, #373030 100%);
    box-shadow: 0px 8px 16px -2px rgba(0, 0, 0, 0.12);
    border-radius: 6px;
    padding: 15px 15px;
    min-width: 146px;
    border: none;
    display: block;
    left: 50%;
    top: 100px;
    opacity: 0;
    visibility: hidden;
    transform: translate(-50%, 0%);
    transition: all 0.4s ease;
 }    //transform: rotateX(90deg)//

  .dropdown-menu:after {
    content: '';
    display: block;
    background: #47413D;
    position: absolute;
    top: -7px;
    left: 45%;
    width: 16px;
    height: 16px;
    border-radius: 2px;
    transform: rotate(45deg); }



  .dropdown-item {
    opacity: 0.65;
    padding: .25rem 15px; }

  .dropdown-item:hover {
    opacity: 1; }

  .dropdown-item:focus, .dropdown-item:hover {
    background: none; }

  .dropdown-menu-outer {
    display: flex; }

  .dropdown-toggle::after {
    border: none; }

  .nav-icon {
    width: 20px;
    height: 13px;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    display: inline-block;
    position: relative;
    position: absolute;
    right: 15px;
    top: 36px;
    transition: all .25s ease-in-out;
    z-index: 1; }

  .nav-icon:after, .nav-icon:before {
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    position: absolute;
    top: 5px;
    left: 50%;
    background: #fff;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    transition: -webkit-transform .25s ease-in-out;
    transition: transform .25s ease-in-out;
    transition: -webkit-transform .25s ease-in-out; }

  .nav-icon.toggler:after {
    -webkit-transform: translate(-50%, -50%) rotate(-45deg);
    transform: translate(-50%, -50%) rotate(-45deg); }

  .nav-icon.toggler:before {
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
    transform: translate(-50%, -50%) rotate(45deg); }

  .nav-icon.toggler {
    border-color: transparent; }

  @media (max-width: 930px) {
    background: #1E1D1D;
    height: auto;

    .mahadao-logo {
      padding-left: 15px; }

    a#navbardrop {
      position: relative; }

    a#navbardrop:after {
      content: '';
      background: url("../img/dropmenu-tringle.svg") 0 no-repeat;
      width: 18px;
      height: 11px;
      position: absolute;
      right: 17px;
      top: 17px; }

    .dropdown.show a#navbardrop:after {
      transform: rotate(-180deg); }

    .navbar-nav {
      height: 100vh; }

    .nav-item.dropdown.show {
      height: auto; }

    .navbar-toggler {
      outline: none; }

    .dropdown-menu-outer {
      justify-content: space-between; }

    .dropdown-menu {
      display: none;
      background: none;
      box-shadow: none;
      transform: inherit;
      opacity: 1;
      padding: 0;
      visibility: visible; }

    .dropdown-menu.show {
      display: block; }

    .dropdown-menu:after {
      height: 0; }

    .nav-link {
      font-size: 16px; }

    .nav-link.btn-orange {
      margin-left: 0px;
      display: block; }

    .nav-item {
      padding-bottom: 2px;
      padding-top: 2px; }

    .navbar-nav li:first-child {
      border-top: 1px solid rgba(255, 255, 255, 0.08);
      margin-top: 18px;
      padding-top: 15px; }

    .navbar-nav li:last-child {
      border: none;
      margin-left: 18px;
      margin-right: 18px;
      padding-bottom: 0; }

    .nav-item-gray-btn {
      margin-left: 18px;
      margin-right: 18px;
      padding-bottom: 22px;
      padding-top: 5px;
      border: none; }

    .nav-link, .dropdown-item {
      padding-left: 18px;
      padding-right: 18px;
      margin: 0; }

    .dropdown-item {
      opacity: 0.64;
      color: #f5f5f5; } } }


@media (min-width: 1200px) {
  .container-fluid {
    padding-left: 130px;
    padding-right: 130px; }

  .container {
    max-width: 1200px!important; } }
