#section-title {
  margin-top: 168px;
  margin-bottom: 130px;

  .imagine-link {
    margin-left: 24px; }


  .main-title {
    line-height: 55px; }

  .counter-count {
    color: #FF7F57; }

  .title-down-text {
    color: #A2A2A2;
    margin-top: 44px;
    font-size: 18px;
    font-family: $font-family-heading;
    line-height: 21px;
    display: flex;
    max-width: 400px;
    position: relative;
    margin-bottom: 40px; }


  .line {
    border: 2px solid rgba(216, 55, 71, 1);
    width: 1px;
    display: flex;
    position: absolute;
    height: 100%;
    margin-top: 0;
    margin-bottom: 0; }

  .text-slide-right {
    overflow: hidden;
    width: 450px;
    padding-left: 20px;

    p {
      animation: 3s text-slide-right;
      margin-bottom: 0; } }

  @keyframes text-slide-right {
    from {
      margin-left: -100%;
      width: 100%; } }

  to {
      padding-left: 20px;
      width: 100%; } }


#videoModal {
  top: 10%; }

.modal-dialog {
  margin: 0;
  max-width: 854px;
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  max-width: 854px;
  left: 50%;
  transform: translateX(-50%)!important; }

.modal-content {
  background: none; }

.modal-header {
  border: none;
  padding: 0;
  height: 60px;
  display: inline-block; }

.close-btn {
  width: 50px;
  height: 50px;
  position: relative;
  cursor: pointer;
  float: right; }

.close-btn:before, .close-btn:after {
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg);
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 50%;
  background: #fff; }

.close-btn:after {
  transform: translate(-50%, -50%) rotate(-45deg); }

#video {
  border: 0;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%; }

.video-outer {
  overflow: hidden;
  padding-top: 56.25%;
  position: relative; }

@media (max-width: 740px) {
  .modal-dialog {
    width: 96%; }

  .imagine-link {
    margin-left: 0!important; }

  .imagine-link {
    margin-top: 20px;
    display: inline-block; }

  .sm-center {
    text-align: center; } }
