#section-crose-chain {
  margin-top: 100px;
  text-align: center;
  font-family: $font-family-heading;

  .card-deck {
    display: flex;
    justify-content: space-between;
    color: $color-white; }

  .card {
    border-radius: 26px;
    border: none;
    margin-bottom: 0; }

  .card-title {
    font-size: 24px;
    margin-top: 20px;
    margin-bottom: 20px; }

  .card-body {
    padding-top: 0px; }

  .card-text {
    font-size: 18px;
    font-weight: 500;
    line-height: 1.5;
    color: #969696; }
  .card-text-maha {
    padding-top: 64px; }
  .text-gradient {
    padding-top: 20px; }

  .img-container {
    padding: 48px 40px 30px 40px;
    height: 365px;

    img {
      height: 100%;
      max-width: 100%; }

    @media (max-width: 740px) {
      height: 250px; } }

  .f-text-text {
    font-size: 18px;
    font-weight: 500;
    max-width: 760px;
    line-height: 1.5;
    color: #969696;
    padding: 50px 10px 0px 10px;
    margin: 0 auto;

    @media (max-width: 600px) {
      font-size: 14px;
      padding: 15px; } } }
